  width: 300px;
  background-color: #f1f1f1;
  padding: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .3);
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  border: 2px solid rgba(200, 200, 206, .757);
  border-top: none;
  justify-content: space-around;
  display: flex;
}

.info-name {
  font-weight: 600;
}

.info-value {
  font-weight: 500;
}

.info-desc {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.search-form {
  width: 100%;
  background-color: #e391e0;
  justify-content: center;
  padding: 10px;
  display: flex;
}

.search-form input {
  width: 300px;
  padding: 10px;
  font-size: 18px;
}

.search-form button {
  cursor: pointer;
